    public titleType (link: any): number {
      if(link.label.id == 0) { // a permament tab (e.g. "Vehicle List"). We are going to display its label as "normal" link
        return 0; 
      } else if(link.label.indexOf("id") == 0) { // editing tab with id for title
        return 1; 
      } else { // editing tab with registration plate for title
        return 0; 
      }
    }

