import { NgModule }       from '@angular/core';
import { CommonModule }   from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { 
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDatepickerModule,
    MatDialogModule,
    MatExpansionModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatNativeDateModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSliderModule,
    MatSlideToggleModule,
    MatSnackBarModule,
    MatSortModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,} from '@angular/material';
import { DataSource, CdkTableModule } from '@angular/cdk/table';
import { FormsModule, ReactiveFormsModule }   from '@angular/forms';

/** Importing local components */
import { ScheduleComponent2 }    from './schedule.component';
import { ManageComponent } from './manage.component';
import { ManageConventionalComponent } from './manage-conventional.component';

import { RouterModule, Routes } from '@angular/router';
//import { AuthGuard }            from '../../auth-guard.service';

/** Importing local modules */
import { MultiSelectInputModule } from '../../lib/multi-select-input/multi-select-input.module';
import { SingleSelectInputModule } from '../../lib/single-select-input/single-select-input.module';

export const scheduleRoutes: Routes = [
    {
        path: '',
        component: ScheduleComponent2,
        children: [
            { path: '', pathMatch: 'full', redirectTo: 'manage-conventional' },
            { path: 'manage-conventional', component: ManageConventionalComponent },
        ]
    }
];

@NgModule({
    imports: [
        CommonModule,
        MatAutocompleteModule,
        MatButtonModule,
        MatButtonToggleModule,
        MatCardModule,
        MatCheckboxModule,
        MatChipsModule,
        MatDatepickerModule,
        MatDialogModule,
        MatExpansionModule,
        MatGridListModule,
        MatIconModule,
        MatInputModule,
        MatListModule,
        MatMenuModule,
        MatNativeDateModule,
        MatPaginatorModule,
        MatProgressBarModule,
        MatProgressSpinnerModule,
        MatRadioModule,
        MatRippleModule,
        MatSelectModule,
        MatSidenavModule,
        MatSliderModule,
        MatSlideToggleModule,
        MatSnackBarModule,
        MatSortModule,
        MatTableModule,
        MatTabsModule,
        MatToolbarModule,
        MatTooltipModule,
        CdkTableModule,
        FormsModule,
        ReactiveFormsModule,
        MultiSelectInputModule,
        SingleSelectInputModule,
        RouterModule.forChild(scheduleRoutes)
    ],
    declarations: [
        ScheduleComponent2,
        ManageComponent,
        ManageConventionalComponent 
    ],
    entryComponents: [
    ],
    providers: []
})

export class Schedule2Module {}



