    public getUsers() {
        this.userService._getUsers(0, 1000,).then(users => {
            if(users){
                this.users.length = 0;
                for(let u of users) {
                    this.users.push({value: u.id, text: u.firstName+" "+u.lastName, grade: u.grade});
                }
            }

            this.usersVisibility = 'Show All';
            this.changeUsersVisibility(this.usersVisibility);
        });
    }

    public changeUsersVisibility(option: string) {
        //this.filterUsers(option);
        switch (option) {
            case 'Show All':
                option = '';
                break;
        }

        this.usersVisible.length = 0;
        for(let u of this.users) {
            if(typeof option == 'string' && option.length > 0) {
                if(u.grade == option){
                    this.usersVisible.push({value: u.id, text: u.firstName+" "+u.lastName, grade: u.grade});
                }
            }
            else {
                this.usersVisible.push({value: u.value, text: u.text, grade: u.grade});
            }
        }
    }

