    public invokeContextMenuMonth(ev: any, month: any) {
        let target = ev.target as any;

        while(target.dataset && target.dataset.monthname == null) {
            if(target.parentNode == null) {
                break;
            }
            target = target.parentNode;
        }
        if(target == null) {
            return true;
        }

        if(typeof target.getBoudingClientRect == 'function') {
            let box = target.getBoundingClientRect();
            let left = box.left;
            let top = box.top;
            target.style.position = 'fixed';
            target.style.left = left;
            target.style.top = top;
        }

        return false;
    }

    public initiate2WaySwap(shift: any) {
        console.log(shift);
        let userIds = [];
        for(let u of this.usersModel) {
            userIds.push({id:u.value});
        }

        let date = shift.startTime.get_ISO_8601_Date()+" ("+shift.startTime.getWeekDay()+") ";
        let shiftName = shift.shift;
        let employeeName = shift.assignmentEmployees[0].employee.firstName+" "+shift.assignmentEmployees[0].employee.lastName;
            
        this.swap_struct = {
            type: '2way',
            assignments: [shift],
            assignments_selection: [{
                value: shift.id,
                text: date+" ("+shiftName+" shift) "+employeeName,
            }],
            suggestions: [],
            suggestions_selection: [],
        };
        
        setTimeout(()=>{
            document.querySelector('.user_mode_banner').classList.add('fadeIn');
        }, 0);

        this.schedule2Service.suggestSwap(shift.id, shift.assignmentEmployees[0].employee, userIds, (assignments: any[])=>{

            //
            // Clear all assignments for whole period for all users
            // 
            for(let u_userId in this.user_assignments) {
                for(let t_stamp in this.user_assignments[u_userId]) {
                    this.user_assignments[u_userId][t_stamp] = {};
                }
            }

            let assignment;
            while(assignment = assignments.shift()){

                this.swap_struct.suggestions.push(assignment);
                let day = new TmDate();
                day.date.setTime(assignment.dateFrom);
                let suggestion_name = day.get_ISO_8601_Date()+" ("+
                    day.getWeekDay()+") "+
                    assignment.assignmentEmployees[0].employee.firstName +        
                    assignment.assignmentEmployees[0].employee.lastName;
                this.swap_struct.suggestions_selection.push({id: assignment.id, text: suggestion_name});

                let userId = assignment.assignmentEmployees[0].employee.id;
                let u_userId = 'u_'+userId;
                let timeStamp = assignment.dateFrom;
                let t_stamp = 't_'+timeStamp;

                if(!this.user_assignments[u_userId]) {
                    this.user_assignments[u_userId] = {};
                }

                if(!this.user_assignments[u_userId][t_stamp]){
                    this.user_assignments[u_userId][t_stamp] = {};
                }

                if(assignment.shift == 'Late' || assignment.shift == 'Night' || assignment.shift == 'OC') {
                    this.user_assignments[u_userId][t_stamp]['oc'] = assignment;
                }
                else {
                    this.user_assignments[u_userId][t_stamp]['daylight'] = assignment;
                }
            }

        });
    }

