import { Location } from '@angular/common';
import { Router } from '@angular/router';
// import { ActivatedRoute, Params }  from '@angular/router';
// import 'rxjs/add/operator/switchMap';
// import { MdTabsModule } from '@angular/material';


export class TabbedComponent  {

    /** Stores history of visited tabs */
    protected history: any[] = [];
    /** Stores tabs */
    protected navLinks: NavLink[] = [];
    /** Active tab id */
    public activeId: number;
    /** route to the tabbed component. The child class must set this for the navigation features to work */
    protected componentUrl: string = '';

    constructor(protected location: Location,
                protected router: Router) {}

    protected back() {
        this.history.pop();
        let hisEntry;
        do{
           hisEntry = this.history.pop();
        } while(hisEntry!=null && this.activeId == hisEntry.id);
         

        if(hisEntry == null){
            console.log("wow");
            let defaultUrl = this.getDefaultUrl();
            this.router.navigate([this.componentUrl, defaultUrl]);
        }
        else {
            let isPresent = false   // this is the indication whether the tab exists
            for(let e of this.navLinks) {
                if(e.id == hisEntry.id) {
                    isPresent = true;
                    break;
                }
            }

            if(!isPresent) {
                this.back();
            }
            else {
                this.router.navigate([this.componentUrl+'/'+this.getUrl(hisEntry.id)]);
            }
        }
    }

    public getDefaultUrl() {
        for(let e of this.navLinks) {
            if(e.default == true) {
                return e.ref;
            }
        }
    }

    protected getUrl(id: number) {
        let url = '';
        for(let e of this.navLinks) {
            if(e.id == id) {
                url = e.ref;
                break;
            }
        }
        return url;
    }

    public pushHistory(id: number) {
      this.history.push({id: id});
    }

    public clearInvalidTabs() {
        this.navLinks = this.navLinks.filter((v) => {
            if(typeof v == 'undefined')
                return false;
            else
                return true;
        });
    }
}

export class NavLink {
  label: string;
  ref: string;
  id: number;
  default: boolean;
}



