    private scrollCallbackX = (event: Event) => {

        this.closeContextMenu();

        if((this.scrollbarX.nativeElement.scrollLeft+10) >
            (this.scrolledWidget.nativeElement.clientWidth-this.scrolledParent.nativeElement.clientWidth)) {

            let hoursPerScreen = this.scrolledParent.nativeElement.clientWidth /
                (this._hour_width * this._scale_factor);
            let daysPerScreen = hoursPerScreen / 24;
            let hoursToAdd = hoursPerScreen;
            //this.hours.length = Math.ceil(hoursPerScreen) * 2;
            let date = new TmDate();
            date.setFromTmDate(this.days[this.days.length-1]);
            //date.alterDate(+1);

            let timeLength = new TmLength();
            timeLength.seconds = hoursToAdd * 3600;

            //            let vehicleIds: any[] = [];                // we need to prepare an array containing vehicleIds
            //            for(let v of this.registrarSchedule) {          // in a format suitable for the loadAssignments()
            //                vehicleIds.push({id:v.registrar.id.toString()}); // method
            //            }                                             // .....

            this.loadAssignments(this.days[this.days.length-1], timeLength, []);

            let day;
            for(let i=this.hours[this.hours.length-1]+1; i<hoursToAdd; i++){
                this.hours.push(i % 24);
                if(i % 24 == 0){
                    date.alterDate(+1);
                    day = date.fork();
                    this.days.push(day);
                }
            }
            this.updateTimelineWidth();
        }

        this.scrolledParent.nativeElement.scrollLeft = this.scrollbarX.nativeElement.scrollLeft;
        this.timeHeader.nativeElement.scrollLeft = this.scrollbarX.nativeElement.scrollLeft;

        this.reevaluateCurrentMonth();
    }

    private reevaluateCurrentMonth() {
        let scrollLeft = this.scrolledParent.nativeElement.scrollLeft;
        let daysOffset = Math.floor(scrollLeft / ((this._hour_width*this._scale_factor) * 24));
        this.currentMonth = this.days[daysOffset].getMonth()+" "+this.days[daysOffset].date.getFullYear();
    }

