    public deleteAssignmentLocally(assignmentId: number|string) {
        let vi = null;
        let ai = null;

        let vc = 0;
        let ac = 0;
        SEEK_ASSIGNMENT:
        for(let vehicle of this.registrarSchedule) {
            for (let ass of vehicle.assignments) {
                if(ass.assignmentId == assignmentId) {
                    vi = vc;
                    ai = ac;
                    break SEEK_ASSIGNMENT;
                }
                ac++;
            }
            vc++;
            ac=0;
        }

        if(vi!=null && ai!=null){
            this.registrarSchedule[vi].assignments.splice(ai, 1);
            this.assignment_selected = null;
        }
    }

