    private bubbleSort(array: any[], property: string) {
        for(let i=(array.length -1); i >= 0; i--) {
            for(let j=1; j <=i; j++) {
                if (array[j-1][property]> array[j][property]) {
                    let tmp = array[j-1];
                    array[j-1] = array[j];
                    array[j] = tmp;
                }
            }
        }
    }

    private getMouseClickTarget(ev: MouseEvent) {

        let schedules = document.querySelectorAll(".timeContainer") as any;
        let schedule;
        for(let sch of schedules) {
            let box = sch.getBoundingClientRect();
            if(box.top < ev.clientY && box.height + box.top > ev.clientY) {
                schedule = sch;
                break;
            }
        }

        if(schedule == null) {
            return ev.target;
        }

        let overlapped = [];
        for(let ass of this.registrarSchedule[schedule.dataset.index].assignments) {
            let scrolledParentBox = this.scrolledParent.nativeElement.getBoundingClientRect();
            let assignmentX = (ass.offsetLeft - this.scrollbarX.nativeElement.scrollLeft) + scrolledParentBox.left;
            if(ev.clientX > assignmentX && ev.clientX < assignmentX + ass.width) {
                overlapped.push({...ass, cursorProximity: ev.clientX - assignmentX});
            }
        }

        this.bubbleSort(overlapped, 'cursorProximity');
        let target = document.querySelector("#assignment"+overlapped[0].assignmentId);
        if(this.assignment_selected != null) {
            if(this.assignment_selected.assignmentId == overlapped[0].assignmentId &&
                overlapped[1] != null) {
                target = document.querySelector("#assignment"+overlapped[1].assignmentId);
            }
        }
        return target;
    }

    public assignShift(event: MouseEvent, shift: string) {
        let target = event.currentTarget as any;
        if(!target.classList.contains('disabled')) {
            if(this.assignment_selected) {
                this.assignment_selected.shift = shift;
                this.schedule2Service.saveAssignment(this.assignment_selected, () => {});
            }
            else{
                let assignment = {
                    assignmentId: 0,
                    startTime: (this.contextmenu_struct as any).userData.startTime,
                    shift: shift,
                    assignmentEmployees: [{id:0, employee: {id:(this.contextmenu_struct as any).userData.userId}}],
                    timeLength: (this.contextmenu_struct as any).userData.timeLength,
                    offsetLeft: (this.contextmenu_struct as any).userData.left,
                    width: (this._hour_width * this._scale_factor) * 24,
                };
                this.saveAssignment(assignment);
            }
        }
        this.closeContextMenu();
        //let contextMenu = document.querySelector("#contextMenu") as any;
        //contextMenu.style.display = 'none';
    }

    public revealContextMenu(ev: MouseEvent, context: string, userData: any) {
        let targetCoords = this.elementCoords(ev.target);    

        this.contextmenu_struct.top = ev.clientY;
        this.contextmenu_struct.left = ev.clientX;
        this.contextmenu_struct.visible = true;
        
        if(context == 'assignment') {
            this.assignment_selected = this.getAssignment(userData.id);
            let shifts;
            if(this.assignment_selected.startTime.date.getUTCDay() == 0 || this.assignment_selected.startTime.date.getUTCDay() == 6) {
                shifts = [
                    {label: 'OC'},
                    {label: 'Night'},
                    {label: 'Unset', disabled: false},
                    {label: 'OFF'},
                    {label: 'OffNight'},
                    {label: 'OffLate'},
                ];
            }
            else {
                shifts = [
                    {label: 'Late'},
                    {label: 'Night'},
                    {label: 'Unset', disabled: false},
                    {label: 'OFF'},
                    {label: 'OffNight'},
                    {label: 'OffLate'},
                ];
            }
            if(!(this.contextmenu_struct as any).userData) {
                (this.contextmenu_struct as any).userData = {};
            }
            (this.contextmenu_struct as any).userData.shifts = shifts;
        }
        else if(context == 'timeline'){
            this.assignment_selected = null;
            let eventCoordinates = this.alignAssignment(ev.clientX - targetCoords.x, 'day');
            let startTime = this.computeStartTime(eventCoordinates);
            let shifts;

            if(startTime.date.getUTCDay() == 0 || startTime.date.getUTCDay() == 6) {
                shifts = [
                    {label: 'OC'},
                    {label: 'Night'},
                    {label: 'Unset', disabled: false},
                    {label: 'OFF'},
                    {label: 'OffNight'},
                    {label: 'OffLate'},
                ];
            }
            else {
                shifts = [
                    {label: 'Late'},
                    {label: 'Night'},
                    {label: 'Unset', disabled: false},
                    {label: 'OFF'},
                    {label: 'OffNight'},
                    {label: 'OffLate'},
                ];
            }

            let timeLength = new TmLength();
            timeLength.seconds = 24*3600;
            (this.contextmenu_struct as any).userData = {
                left: eventCoordinates,
                startTime: startTime,
                userId: userData,
                timeLength: timeLength,
                shifts: shifts,
            };
        }

        ev.preventDefault();
        ev.stopImmediatePropagation();
        ev.stopPropagation();
        return false;
    }

