    public reapplyAssignmentVehicleLocally(assignmentId: number) {
        let assignment = this.removeAssignmentLocally(assignmentId);
        let registrarId = assignment.assignmentActivities[0].vehicle.id;

        for(let schedule of this.registrarSchedule) {
            if(schedule.registrar.id == registrarId) {
                schedule.assignments.push(assignment);
                break;
            }
        }
    }
