    public onChangeScheduleStartDate(newValue: any) {
        let date = new TmDate();
        date.setFromDate(newValue + " UTC");
        this.initView(date);

        let vehicleIds: string[] = [];
        for(let id in this.selectedUsersSimple.vehicles) {
            if(this.selectedUsersSimple.vehicles[id] == true) {
                vehicleIds.push(id);
            }
        }
        let timeLength = new TmLength();
        timeLength.seconds = this.hours.length * 3600;
        this.loadAssignments(this.days[0], timeLength, vehicleIds);
    }

    public closeAssignmentPanel() {
        if(this.assignment_selected != null) {
            let exSelected = document.querySelector("#assignment"+this.assignment_selected.assignmentId);
            exSelected.classList.remove("assignmentSelected");
        }
        this.assignment_selected = null;
        setTimeout(() => {
            this._scaleView();
            this.applyCustomScrollbars();
        }, 0);
    }

    public assignPanelDetailsScrollCallback (event: Event) {
        let panelScroll = document.querySelector(".assignPanelDetails").scrollTop;
        let elements = document.querySelectorAll(".assignPanelDetails > div.dont-scroll") as any;
        for(let div of elements) {
            div.style.marginTop = panelScroll + "px";
        }
    }

    public alignAssignment(coordinates: number, granularity: string) {
        switch(granularity) {
            case 'hour':
                let remaining = Math.floor(coordinates) % (this._hour_width*this._scale_factor);
                coordinates -= remaining;
                break;
            case 'day':
                remaining = Math.floor(coordinates) % (this._hour_width*24*this._scale_factor);
                coordinates -= remaining;
                break;
        }

        return coordinates;
    }

    public onMouseLeave(event: MouseEvent) {
        (event.target as any).classList.remove("hover");
    }

    public onMouseEnter(event: MouseEvent) {
        let target = event.currentTarget as any;
        if(target.classList.contains('disabled')) {
            return false;
        }

        target.classList.add("hover");
    }

    public releaseContextMenu(event: any) {
        let contextMenu = document.querySelector("#contextMenu") as any;
        contextMenu.style.display = "none";
        return true;
    }

    public preventDefault(ev: Event) {
        ev.preventDefault();
        ev.stopImmediatePropagation();
        ev.stopPropagation();
        return false;
    }

    @HostListener('window:click', [ '$event' ])
    window_click(event: any) {
        this.closeContextMenu();
    }
